'use client';

import { useState, useEffect } from 'react';
import { Save, Upload, Eye } from 'lucide-react';

interface AboutContent {
  hospitalOverview: string;
  vision: string;
  mission: string;
  founderMessage: string;
  infrastructure: string;
  timeline: Array<{
    year: number;
    title: string;
    description: string;
  }>;
  featuredImage: string;
}

export default function AboutAdminPage() {
  const [content, setContent] = useState<AboutContent>({
    hospitalOverview: '',
    vision: '',
    mission: '',
    founderMessage: '',
    infrastructure: '',
    timeline: [],
    featuredImage: '',
  });
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    fetchAboutContent();
  }, []);

  const fetchAboutContent = async () => {
    try {
      // TODO: Implement API call
      setLoading(false);
    } catch (error) {
      console.error('Error fetching about content:', error);
      setLoading(false);
    }
  };

  const handleSave = async () => {
    setSaving(true);
    try {
      // TODO: Implement API call to save
      alert('Content saved successfully!');
    } catch (error) {
      console.error('Error saving content:', error);
      alert('Error saving content');
    } finally {
      setSaving(false);
    }
  };

  const addTimelineItem = () => {
    setContent({
      ...content,
      timeline: [
        ...content.timeline,
        { year: new Date().getFullYear(), title: '', description: '' },
      ],
    });
  };

  const removeTimelineItem = (index: number) => {
    setContent({
      ...content,
      timeline: content.timeline.filter((_, i) => i !== index),
    });
  };

  if (loading) {
    return <div className="p-8 text-center">Loading about content...</div>;
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">About Us Content</h1>
          <p className="text-gray-600 mt-1">Manage About Us page content</p>
        </div>
        <div className="flex gap-3">
          <button className="bg-gray-100 text-gray-700 px-6 py-3 rounded-lg hover:bg-gray-200 flex items-center gap-2 transition-all">
            <Eye className="w-5 h-5" />
            Preview
          </button>
          <button
            onClick={handleSave}
            disabled={saving}
            className="bg-primary text-white px-6 py-3 rounded-lg hover:bg-primary/90 flex items-center gap-2 transition-all shadow-lg hover:shadow-xl disabled:opacity-50"
          >
            <Save className="w-5 h-5" />
            {saving ? 'Saving...' : 'Save Changes'}
          </button>
        </div>
      </div>

      {/* Content Form */}
      <div className="space-y-6">
        {/* Featured Image */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">Featured Image</h2>
          <div className="flex items-center gap-4">
            {content.featuredImage ? (
              <img src={content.featuredImage} alt="Featured" className="w-32 h-32 object-cover rounded-lg" />
            ) : (
              <div className="w-32 h-32 bg-gray-100 rounded-lg flex items-center justify-center">
                <Upload className="w-8 h-8 text-gray-400" />
              </div>
            )}
            <button className="bg-blue-50 text-blue-600 px-4 py-2 rounded-lg hover:bg-blue-100 transition-colors">
              Upload Image
            </button>
          </div>
        </div>

        {/* Hospital Overview */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">Hospital Overview</h2>
          <textarea
            value={content.hospitalOverview}
            onChange={(e) => setContent({ ...content, hospitalOverview: e.target.value })}
            className="w-full h-32 border border-gray-300 rounded-lg p-3 focus:ring-2 focus:ring-primary focus:border-transparent"
            placeholder="Enter hospital overview..."
          />
        </div>

        {/* Vision & Mission */}
        <div className="grid md:grid-cols-2 gap-6">
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold text-gray-900 mb-4">Vision</h2>
            <textarea
              value={content.vision}
              onChange={(e) => setContent({ ...content, vision: e.target.value })}
              className="w-full h-32 border border-gray-300 rounded-lg p-3 focus:ring-2 focus:ring-primary focus:border-transparent"
              placeholder="Enter vision statement..."
            />
          </div>
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold text-gray-900 mb-4">Mission</h2>
            <textarea
              value={content.mission}
              onChange={(e) => setContent({ ...content, mission: e.target.value })}
              className="w-full h-32 border border-gray-300 rounded-lg p-3 focus:ring-2 focus:ring-primary focus:border-transparent"
              placeholder="Enter mission statement..."
            />
          </div>
        </div>

        {/* Founder Message */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">Founder's Message</h2>
          <textarea
            value={content.founderMessage}
            onChange={(e) => setContent({ ...content, founderMessage: e.target.value })}
            className="w-full h-48 border border-gray-300 rounded-lg p-3 focus:ring-2 focus:ring-primary focus:border-transparent"
            placeholder="Enter founder's message..."
          />
        </div>

        {/* Infrastructure */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">Infrastructure</h2>
          <textarea
            value={content.infrastructure}
            onChange={(e) => setContent({ ...content, infrastructure: e.target.value })}
            className="w-full h-32 border border-gray-300 rounded-lg p-3 focus:ring-2 focus:ring-primary focus:border-transparent"
            placeholder="Enter infrastructure details..."
          />
        </div>

        {/* Timeline */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold text-gray-900">Timeline / History</h2>
            <button
              onClick={addTimelineItem}
              className="bg-primary text-white px-4 py-2 rounded-lg hover:bg-primary/90 transition-all"
            >
              Add Timeline Item
            </button>
          </div>
          <div className="space-y-4">
            {content.timeline.map((item, index) => (
              <div key={index} className="border border-gray-200 rounded-lg p-4">
                <div className="grid md:grid-cols-4 gap-4">
                  <input
                    type="number"
                    value={item.year}
                    onChange={(e) => {
                      const newTimeline = [...content.timeline];
                      newTimeline[index].year = parseInt(e.target.value);
                      setContent({ ...content, timeline: newTimeline });
                    }}
                    className="border border-gray-300 rounded-lg p-2 focus:ring-2 focus:ring-primary focus:border-transparent"
                    placeholder="Year"
                  />
                  <input
                    type="text"
                    value={item.title}
                    onChange={(e) => {
                      const newTimeline = [...content.timeline];
                      newTimeline[index].title = e.target.value;
                      setContent({ ...content, timeline: newTimeline });
                    }}
                    className="md:col-span-3 border border-gray-300 rounded-lg p-2 focus:ring-2 focus:ring-primary focus:border-transparent"
                    placeholder="Title"
                  />
                  <textarea
                    value={item.description}
                    onChange={(e) => {
                      const newTimeline = [...content.timeline];
                      newTimeline[index].description = e.target.value;
                      setContent({ ...content, timeline: newTimeline });
                    }}
                    className="md:col-span-3 border border-gray-300 rounded-lg p-2 focus:ring-2 focus:ring-primary focus:border-transparent"
                    placeholder="Description"
                    rows={2}
                  />
                  <button
                    onClick={() => removeTimelineItem(index)}
                    className="bg-red-50 text-red-600 px-4 py-2 rounded-lg hover:bg-red-100 transition-colors"
                  >
                    Remove
                  </button>
                </div>
              </div>
            ))}
            {content.timeline.length === 0 && (
              <p className="text-center text-gray-500 py-8">No timeline items. Click "Add Timeline Item" to create one.</p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
